<template>
  <div id="createUser">
    <form>
      <button class="btn btn-primary mx-4 mt-3" v-on:click="close()">Close</button>
      <div class="row m-2">
        <div class="col">
          <div class="my-2">
            <h5>Username</h5>
            <input v-model="username" type="text" class="form-control" />
          </div>
          <div class="my-2">
            <h5 class="card-title">Password</h5>
            <input v-model="password" type="password" class="form-control" />
          </div>
          <div class="my-2">
            <h5 class="card-title">First Name</h5>
            <input v-model="firstname" type="text" class="form-control" />
          </div>
          <div class="my-2">
            <h5 class="card-title">Last Name</h5>
            <input v-model="lastname" type="text" class="form-control" />
          </div>
          <div class="my-2">
            <h5 class="card-title">Email</h5>
            <input v-model="email" type="text" class="form-control" />
          </div>
          <div class="my-2">
            <h5 class="card-title">Role</h5>
            <input v-model="type" type="text" class="form-control" />
          </div>
          <div>
            <h5 class="card-title">Gender</h5>
            <input v-model="gender" type="text" class="form-control" />
          </div>
          <div class="text-center">
            <button class="btn btn-success m-2" v-on:click="createUser()">SAVE</button>
          </div>
        </div>
      </div>
    </form>
  </div>
</template>

<script>
export default {
  name: "CreateUser",
  data() {
    return {
      username: "",
      password: "",
      firstname: "",
      lastname: "",
      type: "",
      email: "",
      gender: ""
    };
  },
  methods: {
    close() {
      this.$emit("close");
    },
    createUser() {
      const user = {
        username: this.username,
        password: this.password,
        firstname: this.firstname,
        lastname: this.lastname,
        email: this.email,
        type: this.type,
        gender: this.gender
      };
      this.saveUser(user);
    }
  }
};
</script>

<style scoped>
#createUser {
  color: black;
}
</style>