import Vue from 'vue'
import Vuex from 'vuex'
import Axios from 'axios'
import router from '../router';
import cookies from 'vue-cookies'
import moment from "moment"

Vue.use(Vuex)

export default new Vuex.Store({

  state: {
    user: {
      username: null,
      token: null,
      role: null
    },
    currentPage: 0,
    currentPageSize: 10,
    availablePages: null,
    isRegistered: true,

    event: null,
    eventTime: null,
    eventDate: null,
    events: [],

    comments: null,

    userDetail: null,
    users: null,

  },
  mutations: {
    SET_EVENTS: (state, events) => {
      state.events = events
    },
    SET_AVAILABLE_PAGES: (state, availablePages) => {
      state.availablePages = availablePages
    },
    ADD_EVENTS: (state, newEv) => {
      if (state.events != null) {
        state.events = state.events.concat(newEv)
      }
    },
    ADD_COMMENT: (state, comment) => {
      state.event.comments.push({
        content: comment.content,
        createdAt: comment.createdAt,
        author: comment.author
      })
    },
    SET_USERNAME: (state, username) => {
      state.user.username = username;
    },
    SET_IS_REGISTERED: (state, isRegistered) => {
      state.isRegistered = isRegistered;
    },
    SET_AUTHENTICATED: (state, authResponse) => {
      state.user.token = authResponse.token == null ? cookies.get("token") : authResponse.token;
      state.user.role = authResponse.type;
    },
    SET_UNAUTHENTICATED: (state) => {
      cookies.remove("token");
      state.user.token = "";
      state.user.role = "";
    },
    SET_USERS: (state, users) => {
      state.users = users;
    },
    SET_USER: (state, user) => {
      state.userDetail = user;
    },
    SET_EVENT: (state, payload) => {
      state.event = payload;
      if (payload != null) {
        state.event.eventTimeParsed = moment(new Date(state.event.eventTime)).format("hh:mm");
        state.event.eventDate =
          state.event.eventTime &&
          new Date(state.event.eventTime).toISOString().split("T")[0];
      }
    },
    REMOVE_EVENT: (state, id) => {
      state.events = state.events.filter(function (event) {
        return event.id != id;
      }),
        state.event = null;
    },
    SET_CURRENT_PAGE_SIZE: (state, currentPageSize) => {
      state.currentPageSize = currentPageSize;
    },
    SET_FIRST_PAGE: (state) => {
      state.currentPage = 0;
    },
    SET_NEXT_PAGE: (state) => {
      state.currentPage += 1;
    },
    SET_PREV_PAGE: (state) => {
      state.currentPage -= 1;
    },
    REMOVE_COMMENT: (state, commentId) => {
      state.event.comments = state.event.comments.filter(function (comment) {
        return comment.id != commentId;
      })
    },
    SET_COMMENTS: (state, payload) => {
      state.comments = payload
    }
  },

  actions: {
    //User APIs
    userSignin: ({ commit }, input) => {
      Axios.post('api/user/authenticate',
        {
          "username": '' + input.username,
          "password": '' + input.password
        })
        .then(data => {
          Vue.toasted.show("Logged in", {
            theme: "outline",
            position: "top-center",
            duration: 2000
          });
          commit('SET_AUTHENTICATED', data.data);
          commit("SET_USERNAME", input.username)
          router.push('/')
          cookies.set("token", data.data.token, "30MIN");
          console.log(data.data);
        })
        .catch(error => {
          Vue.toasted.show("Incorrect credentials", {
            theme: "outline",
            position: "top-center",
            duration: 2000
          });
          console.log(error);
        })
    },
    adminSignin({ commit }, input) {
      Axios.post('api/user/authenticate',
        {
          "username": '' + input.username,
          "password": '' + input.password
        })
        .then(data => {
          Vue.toasted.show("Logged in", {
            theme: "outline",
            position: "top-center",
            duration: 2000
          });
          commit('SET_AUTHENTICATED', data.data);
          commit("SET_USERNAME", input.username)

          router.push('admin/events')
          cookies.set("token", data.data.token, "30MIN");

          console.log(data.data);
        })
        .catch(error => {
          Vue.toasted.show("Incorrect credentials", {
            theme: "outline",
            position: "top-center",
            duration: 2000
          });
          console.log(error);
        })
    },
    logingWithStoredToken({ commit }) {
      const token = cookies.get("token");
      var tokenHeader = {
        headers: { 'Authorization': token }
      };
      Axios.get('api/user/authenticate', tokenHeader)
        .then(data => {
          Vue.toasted.show("Logged in", {
            theme: "outline",
            position: "top-center",
            duration: 2000
          });
          commit('SET_AUTHENTICATED', data.data);
          router.push('admin/events')
          console.log(data.data);
        })
        .catch(error => {
          Vue.toasted.show("Unauthorized access", {
            theme: "outline",
            position: "top-center",
            duration: 2000
          });
          console.log(error);
        })
    },
    loginUserWithStoredToken({ commit }) {
      const token = cookies.get("token");
      if (token == null) {
        return
      }
      var tokenHeader = {
        headers: { 'Authorization': token }
      };
      Axios.get('api/user/authenticate', tokenHeader)
        .then(data => {
          Vue.toasted.show("Logged in", {
            theme: "outline",
            position: "top-center",
            duration: 2000
          });
          commit('SET_AUTHENTICATED', data.data);
          router.push('/')
          console.log(data.data);
        })
        .catch(error => {
          Vue.toasted.show("Unauthorized access", {
            theme: "outline",
            position: "top-center",
            duration: 2000
          });
          console.log(error);
        })
    },
    saveUser({ commit }, payload) {
      Axios.post('api/user/register', payload)
        .then(data => {
          Vue.toasted.show("Succesfully registered ", {
            theme: "outline",
            position: "top-center",
            duration: 2000
          });
          commit('SET_USERNAME', data.data);
          commit('SET_IS_REGISTERED', true);
          console.log(data.data);
        })
        .catch(error => {
          Vue.toasted.show("Username taken", {
            theme: "outline",
            position: "top-center",
            duration: 2000
          });
          console.log(error);
        })
    },
    userLogout: ({ commit }) => {
      commit("SET_UNAUTHENTICATED");
      router.push("/")
      Vue.toasted.show("Logged out", {
        theme: "outline",
        position: "top-center",
        duration: 2000
      });
    },
    loadUsers: ({ commit }) => {
      Axios.get('api/user/secured/users')
        .then(data => {
          commit('SET_USER', null);
          commit('SET_USERS', data.data);
          console.log(data.data);
        })
        .catch(error => {
          Vue.toasted.show("Error", {
            theme: "outline",
            position: "top-center",
            duration: 2000
          });
          console.log(error);
        })
    },
    loadUserDetail: ({ commit }, username) => {
      Axios.get('api/user/secured/' + username)
        .then(data => {
          commit('SET_USER', data.data);
          console.log(data.data);
        })
        .catch(error => {
          Vue.toasted.show(error, {
            theme: "outline",
            position: "top-center",
            duration: 2000
          });
          console.log(error);
        })
    },
    editUser({ commit }, payload) {
      var token = {
        headers: { 'Authorization': "Bearer " + this.state.user.token }
      };
      Axios.put('api/user/secured/' + this.state.userDetail.username,
        payload, token).then(data => {
          commit('SET_USER', data.data)
          console.log(data.data);

        }).catch(error => {
          Vue.toasted.show(error, {
            theme: "outline",
            position: "top-center",
            duration: 2000
          });
          console.log(error);
        })
    },
    deleteCurrentUser() {
      var token = {
        headers: { 'Authorization': "Bearer " + this.state.user.token }
      };
      Axios.delete('api/user/secured/' + this.state.userDetail.username,
        token).then(
          console.log("deleted")
        ).catch(error => {
          Vue.toasted.show(error, {
            theme: "outline",
            position: "top-center",
            duration: 2000
          });
          console.log(error);
        })
    },
    searchUser({ commit }, searchValue) {
      var token = {
        headers: { 'Authorization': "Bearer " + this.state.user.token }
      };
      Axios.post('api/user/secured/users', { searchValue: searchValue }, token).then(data => {
        commit('SET_USERS', data.data)
        console.log(data.data);
      }).catch(error => {
        Vue.toasted.show(error, {
          theme: "outline",
          position: "top-center",
          duration: 2000
        });
        console.log(error);
      })
    },
    loadComments({ commit }) {
      var token = {
        headers: { 'Authorization': "Bearer " + this.state.user.token }
      };
      Axios.get('api/user/secured/' + this.state.userDetail.username + "/comments",
        token).then(data => {
          commit('SET_COMMENTS', data.data)
          console.log(data.data);

        }).catch(error => {
          Vue.toasted.show(error, {
            theme: "outline",
            position: "top-center",
            duration: 2000
          });
          console.log(error);
        })
    },
    //Event APIs
    loadEvents({ commit }) {
      Axios.get('api/event/events?p=' + this.state.currentPage + '&s=' + this.state.currentPageSize)
        .then(data => {
          commit('SET_EVENT', null);
          commit('SET_EVENTS', data.data.eventsList);
          router.push("/admin/events");
          console.log(data.data);
        })
        .catch(error => {
          Vue.toasted.show(error, {
            theme: "outline",
            position: "top-center",
            duration: 2000
          });
          console.log(error);
        })
    },
    loadMoreEvents({ commit }, size) {
      Axios.get('api/event/events?p=' + this.state.currentPage + '&s=' + size)
        .then(data => {
          commit('ADD_EVENTS', data.data.eventsList);
          console.log(data.data);
        })
        .catch(error => {
          Vue.toasted.show(error, {
            theme: "outline",
            position: "top-center",
            duration: 2000
          });
          console.log(error);
        })
    },
    loadMoreEventsForUser({ commit }) {
      Axios.get('api/event/events?p=' + this.state.currentPage + '&s=' + this.state.currentPageSize)
        .then(data => {
          commit('SET_EVENTS', data.data);
          console.log(data.data);
        })
        .catch(error => {
          Vue.toasted.show(error, {
            theme: "outline",
            position: "top-center",
            duration: 2000
          });
          console.log(error);
        })
    },
    loadEventsByType({ commit }, payload) {
      Axios.get('api/event/events/' + payload.type + '?p=' + this.state.currentPage + '&s=' + payload.selected)
        .then(data => {
          commit('SET_EVENTS', data.data.eventsList);
          commit('SET_AVAILABLE_PAGES', data.data.availablePages)
          console.log(data.data);
        })
        .catch(error => {
          Vue.toasted.show(error, {
            theme: "outline",
            position: "top-center",
            duration: 2000
          });
          console.log(error);
        })
    },
    searchEventsBySearchValue({ commit }, searchValue) {
      Axios.post('api/event/events/search' + '?p=' + this.state.currentPage + '&s=' + this.state.currentPageSize, { searchValue: searchValue })
        .then(data => {
          commit('SET_EVENTS', data.data.eventsList);
          commit('SET_AVAILABLE_PAGES', data.data.availablePages)
          console.log(data.data);
        })
        .catch(error => {
          Vue.toasted.show(error, {
            theme: "outline",
            position: "top-center",
            duration: 2000
          });
          console.log(error);
        })
    },
    loadEventDetail({ commit }, id) {
      Axios.get('api/event/' + id)
        .then(data => {
          commit('SET_EVENT', data.data)
          console.log(data.data);
        })
        .catch(error => {
          Vue.toasted.show(error, {
            theme: "outline",
            position: "top-center",
            duration: 2000
          });
          console.log(error);
        })
    },
    loadEventsForUsers({ commit }) {
      Axios.get('api/event/events?p=' + this.state.currentPage + '&s=' + this.state.currentPageSize)
        .then(data => {
          commit('SET_EVENTS', data.data.eventsList);
          commit('SET_AVAILABLE_PAGES', data.data.availablePages);
          console.log(data.data);
        })
        .catch(error => {
          Vue.toasted.show(error, {
            theme: "outline",
            position: "top-center",
            duration: 2000
          });
          console.log(error);
        })
    },
    addEvent({ commit }, payload) {
      var token = {
        headers: { 'Authorization': "Bearer " + this.state.user.token }
      };
      Axios.post('api/event/secured/', payload, token).then(
        Vue.toasted.show("Created", {
          theme: "outline",
          position: "top-center",
          duration: 2000
        },
          commit)
      )
        .catch(error => {
          Vue.toasted.show(error, {
            theme: "outline",
            position: "top-center",
            duration: 2000
          });
          console.log(error);
        })
    },
    editEventDetail({ commit }, payload) {
      var token = {
        headers: { 'Authorization': "Bearer " + this.state.user.token }
      };
      Axios.put('api/event/secured/' + payload.id, payload, token).then(data => {
        commit("SET_EVENT", data.data)
      })
        .catch(error => {
          Vue.toasted.show(error, {
            theme: "outline",
            position: "top-center",
            duration: 2000
          });
          console.log(error);
        })
    },
    removeCurrentEvent({ commit }, eventId) {
      var token = {
        headers: { 'Authorization': "Bearer " + this.state.user.token }
      };
      Axios.delete('api/event/secured/' + eventId, token).then(
        Vue.toasted.show("Removed", {
          theme: "outline",
          position: "top-center",
          duration: 2000
        }),
        commit("REMOVE_EVENT", eventId),
        router.push("/")

      ).setTimeout.catch(error => {
        Vue.toasted.show(error, {
          theme: "outline",
          position: "top-center",
          duration: 2000
        });
        console.log(error);
      })
    },

    //COMMENT APIs
    addComment({ commit }, comment) {
      if (this.state.user.token == null) {
        Vue.toasted.show("Log in to comment", {
          theme: "outline",
          position: "top-center",
          duration: 2000
        });
        return;
      }
      var token = {
        headers: { 'Authorization': "Bearer " + this.state.user.token }
      };
      Axios.post('api/event/secured/' + this.state.event.id + "/comment", { comment: comment }, token).then(data => {
        commit("ADD_COMMENT", data.data),
          console.log("created")
      })
        .catch(error => {
          Vue.toasted.show(error, {
            theme: "outline",
            position: "top-center",
            duration: 2000
          });
          console.log(error);
        })

    },
    deleteCommentById({ commit }, payload) {
      var token = {
        headers: { 'Authorization': "Bearer " + this.state.user.token }
      };
      Axios.delete('api/event/secured/comment/' + payload, token).then(
        commit("REMOVE_COMMENT", payload),
        console.log("deleted"))
        .catch(error => {
          Vue.toasted.show(error, {
            theme: "outline",
            position: "top-center",
            duration: 2000
          });
          console.log(error);
        })
    }
  },
  modules: {
  }

})
