<template>
  <div class="card" v-if="userDetail.username != null">
    <form>
      <button class="btn btn-primary mx-4 mt-3" v-on:click="showUserList()">Back</button>
      <div class="row m-2">
        <div class="col">
          <div class="my-2">
            <h5>Username</h5>
            <input v-model="userDetail.username" type="text" class="form-control" :disabled="true" />
          </div>
          <div class="my-2">
            <h5 class="card-title">First Name</h5>
            <input
              v-model="userDetail.firstname"
              type="text"
              class="form-control"
              :disabled="!isCreatingNewUser"
            />
          </div>
          <div class="my-2">
            <h5 class="card-title">Last Name</h5>
            <input
              v-model="userDetail.lastname"
              type="text"
              class="form-control"
              :disabled="!isCreatingNewUser"
            />
          </div>
          <div class="my-2">
            <h5 class="card-title">Email</h5>
            <input
              v-model="userDetail.email"
              type="text"
              class="form-control"
              :disabled="!isCreatingNewUser"
            />
          </div>
          <div class="my-2">
            <h5 class="card-title">Role</h5>
            <input
              v-model="userDetail.type"
              type="text"
              class="form-control"
              :disabled="!isCreatingNewUser"
            />
          </div>
          <div>
            <h5 class="card-title">Gender</h5>
            <input
              v-model="userDetail.gender"
              type="text"
              class="form-control"
              :disabled="!isCreatingNewUser"
            />
          </div>
          <div class="my-2">
            <h5 class="card-title">Comments</h5>
            <button class="btn btn-primary" @click="showComments()">Show comments</button>
          </div>
          <div class="text-center">
            <button
              class="btn btn-primary m-2"
              :disabled="isCreatingNewUser"
              v-on:click="isCreatingNewUser = true"
            >EDIT</button>
            <button
              class="btn btn-success m-2"
              :disabled="!isCreatingNewUser"
              v-on:click="edit()"
            >SAVE</button>
            <button class="btn btn-danger m-2" @click="deleteUser()">DELETE</button>
          </div>
        </div>
        <div class="col">
          <div v-if="comments != null && comments.length == 0 ">
            <h5>No comments</h5>
          </div>
          <div v-else-if="comments != null">
            <table class="table my-2">
              <thead>
                <tr>
                  <th scope="col">Comment</th>
                  <th scope="col">Created</th>
                </tr>
              </thead>
              <tbody v-for="comment in comments" :key="comment.id">
                <tr>
                  <td class="p-0 m-0">{{comment.content}}</td>
                  <td class="p-0 m-0">{{formatDate(comment.createdAt)}}</td>
                </tr>
              </tbody>
            </table>
          </div>
        </div>
      </div>
    </form>
  </div>
</template>

<script>
import { mapState, mapActions, mapMutations } from "vuex";
import moment from "moment";
export default {
  data() {
    return {};
  },
  props: ["isCreatingNewUser"],
  computed: {
    ...mapState(["user", "userDetail", "users", "currentPageSize", "comments"])
  },
  methods: {
    ...mapActions([
      "loadUsers",
      "editUser",
      "loadComments",
      "deleteCurrentUser",
      "saveUser"
    ]),
    ...mapMutations(["SET_CURRENT_PAGE_SIZE", "SET_USER", "SET_COMMENTS"]),

    showUserList() {
      this.SET_CURRENT_PAGE_SIZE(this.currentPageSize);
      this.loadUsers();
      this.SET_USER(null);
      this.SET_COMMENTS(null);
    },
    edit() {
      const user = {
        username: this.userDetail.username,
        firstname: this.userDetail.firstname,
        lastname: this.userDetail.lastname,
        email: this.userDetail.email,
        type: this.userDetail.type,
        gender: this.userDetail.gender
      };
      this.isCreatingNewUser = false;
      this.editUser(user);
    },
    showComments() {
      this.loadComments();
    },
    deleteUser() {
      this.deleteCurrentUser();
      this.showUserList();
    },
    formatDate(date) {
      return moment(new Date(date)).format("LL");
    }
  }
};
</script>

<style scoped>
</style>