<template>
  <div class="card mx-4" v-if="event != null">
    <form class="row">
      <div class="col-6 m-3">
        <button class="btn btn-primary my-2" v-on:click="showEvents()">Back</button>
        <div class="my-2">
          <h5>Title</h5>
          <input
            v-model="event.title"
            type="text"
            class="form-control"
            :disabled="!isCreatingNewEvent"
            required="required"
          />
        </div>
        <div class="my-2">
          <h5>Type</h5>
          <input
            v-model="event.type"
            type="text"
            class="form-control w-50"
            :disabled="!isCreatingNewEvent"
            required="required"
          />
        </div>
        <div class="my-2">
          <h5>Author</h5>
          <input
            v-model="event.author"
            type="text"
            class="form-control w-50"
            :disabled="!isCreatingNewEvent"
            required="required"
          />
          <button
            type="submit"
            :disabled="!isCreatingNewEvent"
            class="btn btn-primary my-2"
            @click="showFindUser()"
          >Search user</button>
          <modals-container />
        </div>
        <div class="my-2">
          <h5>Creation date</h5>
          <input
            class="form-control w-50"
            :disabled="true"
            :value="event.createdAt && new Date(event.createdAt).toISOString().split('T')[0]"
            type="date"
          />
        </div>
        <div class="my-2">
          <h5>Event date</h5>
          <div class="row">
            <input
              class="form-control w-50 mx-3"
              type="date"
              :disabled="!isCreatingNewEvent"
              v-model="event.eventDate"
            />
            <input
              class="form-control w-25"
              type="time"
              :disabled="!isCreatingNewEvent"
              v-model="event.eventTimeParsed"
            />
          </div>
        </div>
        <div class="my-2">
          <h5>About</h5>
          <textarea
            v-model="event.content"
            class="form-control form-group"
            :disabled="!isCreatingNewEvent"
            rows="3"
            required="required"
          ></textarea>
        </div>
        <div class="my-2">
          <h5>Comments</h5>
          <button class="btn btn-primary" @click="showComments()">Show comments</button>
        </div>
        <div v-if="isShowingComments">
          <table
            id="dtBasicExample"
            class="table table-striped table-bordered table-sm text-left"
            cellspacing="0"
            width="100%"
          >
            <thead class="thead-light">
              <tr>
                <th scope="col">Username</th>
                <th scope="col">Comment</th>
                <th scope="col">Time created</th>
                <th class="bg-white border-white" v-if="isCreatingNewEvent" scope="col"></th>
              </tr>
            </thead>
            <tbody v-for="comment in event.comments" :key="comment.id">
              <tr>
                <th class="text-center" scope="row">{{ comment.author }}</th>
                <td>
                  <p>{{ comment.content }}</p>
                </td>
                <td>
                  <p>{{ formatDate(comment.createdAt) }}</p>
                </td>
                <td v-if="isCreatingNewEvent" class="bg-white border-white">
                  <button class="btn btn-danger" @click="deleteComment(comment.id)">Delete comment</button>
                </td>
              </tr>
            </tbody>
          </table>
        </div>
        <div class="text-center">
          <button
            class="btn btn-primary m-2"
            :disabled="isCreatingNewEvent"
            v-on:click="isCreatingNewEvent = true"
          >EDIT</button>
          <button
            class="btn btn-success m-2"
            :disabled="!isCreatingNewEvent"
            v-on:click="editEvent()"
          >SAVE</button>
          <button class="btn btn-danger m-2" v-on:click="deleteEvent">DELETE</button>
        </div>
      </div>
      <div class="col-5 my-4 p-0">
        <GMap />
      </div>
    </form>
  </div>
</template>

<script>
import { mapActions, mapState, mapMutations } from "vuex";
import moment from "moment";
import FindUser from "@/components/FindUser.vue";
import GMap from "@/components/GMap.vue";

export default {
  name: "AdminDetailEvent",
  data() {
    return {
      isShowingComments: false,
      title: "",
      type: "",
      author: "",
      content: "",
      createdAt: ""
      // eventDate: "",
      // eventTime: "00:00"
    };
  },
  created() {
    if (event == null) {
      this.loadEventDetail(this.$route.params.eventId);
    }
  },
  components: {
    GMap
  },
  props: ["isCreatingNewEvent"],
  computed: {
    ...mapState(["event"])
  },
  methods: {
    ...mapActions([
      "loadEventDetail",
      "addEvent",
      "removeCurrentEvent",
      "loadEvents",
      "editEventDetail",
      "deleteCommentById"
    ]),
    ...mapMutations(["SET_FIRST_PAGE", "SET_USERS"]),

    showEvents() {
      this.SET_FIRST_PAGE();
      this.loadEvents();
    },
    editEvent() {
      const event = {
        id: this.event.id,
        title: this.event.title,
        type: this.event.type,
        author: this.event.author,
        content: this.event.content,
        createdAt: this.event.createdAt,
        eventTime:
          this.event.eventDate.split("T")[0] +
          "T" +
          this.event.eventTimeParsed +
          "Z",
        latitude: this.event.latitude,
        longitude: this.event.longitude
      };
      console.log(event);
      this.editEventDetail(event);
    },
    deleteEvent() {
      this.SET_FIRST_PAGE();
      this.removeCurrentEvent(this.event.id);
    },
    showComments() {
      this.isShowingComments = this.isShowingComments == true ? false : true;
    },
    deleteComment(commentId) {
      this.deleteCommentById(commentId);
    },
    formatDate(date) {
      return moment(date).format("LLL");
    },

    showFindUser() {
      this.$modal.show(
        FindUser,
        {
          time: this.time
        },
        {
          height: "auto",
          scrollable: true
        },
        {
          "before-close": this.beforeClose
        }
      );
    },
    beforeClose() {
      this.SET_USERS(null);
    }
  }
};
</script>

