<template>
  <div v-if="event != null">
    <GmapMap
      :center="{lat: parseFloat(event.latitude),lng: parseFloat(event.longitude)}"
      :zoom="15"
      style="width: 110%; height: 700px"
    >
      <GmapMarker
        :position="{lat: parseFloat(event.latitude),lng: parseFloat(event.longitude)}"
        :clickable="true"
        :draggable="true"
        @drag="updateCoordinates"
        @dragend="gMapFunc"
      />
    </GmapMap>
  </div>
</template>


<script>
import { mapState } from "vuex";
import { gmapApi } from "vue2-google-maps";

export default {
  data() {
    return {
      position: {
        lat: 50.077555,
        lng: 14.436008
      }
    };
  },
  computed: {
    ...mapState(["event"]),
    google: gmapApi
  },
  methods: {
    updateCoordinates(location) {
      this.position.lat = location.latLng.lat();
      this.position.lng = location.latLng.lng();
    },
    gMapFunc(location) {
      this.event.latitude = location.latLng.lat();
      this.event.longitude = location.latLng.lng();
    }
  }
};
</script>

