<template>
  <div v-if="event == null" class="bg-light">
    <GmapMap
      :center="{lat: 50.077555,lng:  14.436008}"
      :zoom="12"
      style="width: 100%; height: calc(100vh - 50px)"
    >
      <gmap-info-window
        :options="infoOptions"
        :position="infoWindowPos"
        :opened="infoWinOpen"
        @closeclick="infoWinOpen=false"
      >
        <div class="text-center">
          <p class="m-2">{{infoContent}}</p>
          <button class="btn btn-primary" @click="showEventDetail">Detail</button>
        </div>
      </gmap-info-window>

      <div v-for="event in events" :key="event.id">
        <GmapMarker
          :position="{lat: parseFloat(event.latitude),lng: parseFloat(event.longitude)}"
          :clickable="true"
          :draggable="false"
          @click="toggleInfoWindow(event,event.id)"
        />
      </div>
    </GmapMap>
  </div>
  <div v-else class="bg-light">
    <GmapMap
      :center="{lat: parseFloat(event.latitude),lng: parseFloat(event.longitude)}"
      :zoom="15"
      style="width: 100%; height: calc(100vh - 50px)"
    >
      <GmapMarker
        :position="{lat: parseFloat(event.latitude),lng: parseFloat(event.longitude)}"
        :clickable="true"
        :draggable="true"
        @drag="updateCoordinates"
        @dragend="gMapFunc"
      />
    </GmapMap>
  </div>
</template>

<script>
import { mapState, mapActions } from "vuex";
import { gmapApi } from "vue2-google-maps";

export default {
  data() {
    return {
      infoContent: "",
      infoWindowEventId: "",
      infoWindowPos: null,
      infoWinOpen: false,
      currentMidx: null,
      //optional: offset infowindow so it visually sits nicely on top of our marker
      infoOptions: {
        pixelOffset: {
          width: 0,
          height: -35
        }
      }
    };
  },
  computed: {
    ...mapState(["event", "events"]),
    google: gmapApi
  },
  methods: {
    ...mapActions(["loadEventDetail"]),

    updateCoordinates(location) {
      this.position.lat = location.latLng.lat();
      this.position.lng = location.latLng.lng();
    },
    showDetailForMarker(marker) {
      console.log(marker);
    },
    toggleInfoWindow: function(event, idx) {
      this.infoWindowPos = {
        lat: parseFloat(event.latitude),
        lng: parseFloat(event.longitude)
      };
      this.infoWindowEventId = event.id;
      this.infoContent = event.title;
      //check if its the same marker that was selected if yes toggle
      if (this.currentMidx == idx) {
        this.infoWinOpen = !this.infoWinOpen;
      }
      //if different marker set infowindow to open and reset current marker index
      else {
        this.infoWinOpen = true;
        this.currentMidx = idx;
      }
    },
    showEventDetail() {
      this.loadEventDetail(this.infoWindowEventId);
    }
  }
};
</script>

