<template>
  <div class="userEvents pt-4 mx-4">
    <div class="search-area text-center">
      <div class="p-1 rounded rounded-pill shadow-sm mb-4">
        <div class="input-group">
          <input
            type="search"
            placeholder="What event are you looking for?"
            aria-describedby="button-addon1"
            class="form-control border-0 bg-light"
            v-model="searchValue"
          />
          <div class="input-group-append">
            <button class="btn btn-primary" @click="loadEventsBySearchValue">Search</button>
          </div>
        </div>
      </div>
    </div>
    <div class="tags-toggler">
      <div class="btn-group btn-group-toggle mb-2" data-toggle="buttons">
        <button class="btn active" v-on:click="loadEventsByTypeSelected('')">
          <input type="radio" name="options" id="option1" autocomplete="off" checked /> ALL
        </button>
        <button class="btn" v-on:click="loadEventsByTypeSelected('MUSIC')">
          <input type="radio" name="options" id="option1" autocomplete="off" /> MUSIC
        </button>
        <button class="btn" v-on:click="loadEventsByTypeSelected('PARTY')">
          <input type="radio" name="options" id="option2" autocomplete="off" /> PARTY
        </button>
        <button class="btn" v-on:click="loadEventsByTypeSelected('SPORT')">
          <input type="radio" name="options" id="option3" autocomplete="off" /> SPORT
        </button>
        <button class="btn" v-on:click="loadEventsByTypeSelected('CHILL')">
          <input type="radio" name="options" id="option3" autocomplete="off" /> CHILL
        </button>
        <button class="btn" v-on:click="loadEventsByTypeSelected('MOVIE')">
          <input type="radio" name="options" id="option3" autocomplete="off" /> MOVIE
        </button>
      </div>
    </div>
    <table class="table m-0 p-0">
      <thead>
        <tr>
          <th class="p-1 m-0">#</th>
          <th class="p-1 m-0">Title</th>
          <th class="p-1 m-0">Author</th>
          <th class="p-1 m-0">Type</th>
          <th />
        </tr>
      </thead>
      <tbody>
        <tr v-for="(event, index) in events" v-bind:key="index">
          <td class="p-1 m-0">{{ event.id }}</td>
          <td class="p-1 m-0">{{ event.title }}</td>
          <td class="p-1 m-0">{{ event.author }}</td>
          <td class="p-1 m-0">{{ event.type}}</td>
          <td class="p-1 m-0">
            <button class="btn-sm btn-primary" v-on:click="showDetailForEvent(event.id)">detail</button>
          </td>
        </tr>
      </tbody>
    </table>
    <div class="text-center pt-4">
      <button
        :disabled=" currentPage <= 0 "
        v-on:click="loadPreviousPage()"
        class="btn btn-primary btn-sm mx-2"
      >Previous</button>
      <button
        :disabled="(currentPage+1) == availablePages"
        v-on:click="loadNextPage()"
        class="btn btn-primary btn-sm mx-2"
      >Next</button>
    </div>
    <div v-if="user.token != null">
      <button class="btn btn-primary my-4 w-100" v-on:click="showCreateEvent()">Create new Event</button>
      <modals-container />
    </div>
  </div>
</template>

<script>
import { mapState, mapActions, mapMutations } from "vuex";
import CreateEvent from "@/components/CreateEvent.vue";

export default {
  name: "UserEvents",
  data() {
    return {
      isActive: false,
      type: "",
      searchValue: ""
    };
  },
  computed: {
    ...mapState([
      "isEventChosen",
      "events",
      "event",
      "currentPage",
      "currentPageSize",
      "availablePages",
      "user"
    ])
  },

  methods: {
    ...mapActions([
      "loadEventDetail",
      "loadEventsForUsers",
      "loadEventsByType",
      "loadMoreEventsForUser",
      "searchEventsBySearchValue"
    ]),
    ...mapMutations(["SET_NEXT_PAGE", "SET_PREV_PAGE", "SET_FIRST_PAGE"]),

    showDetailForEvent(eventId) {
      this.loadEventDetail(eventId);
    },
    loadNextPage: function() {
      this.SET_NEXT_PAGE();
      if (this.type == "") {
        this.loadEventsForUsers(this.currentPageSize);
      } else {
        this.loadEventsByType({
          type: this.type,
          selected: this.currentPageSize
        });
      }
    },
    loadEventsBySearchValue() {
      this.searchEventsBySearchValue(this.searchValue);
    },
    loadPreviousPage: function() {
      this.SET_PREV_PAGE();
      if (this.type == "") {
        this.loadEventsForUsers(this.currentPageSize);
      } else {
        this.loadEventsByType({
          type: this.type,
          selected: this.currentPageSize
        });
      }
    },
    loadEventsForPage: function() {
      this.loadEventsByType(this.currentPageSize);
    },
    loadEventsByTypeSelected(type) {
      this.SET_FIRST_PAGE();
      this.type = type;
      console.log(type);
      if (type == "") {
        this.loadEventsForUsers();
      } else {
        this.loadEventsByType({ type: type, selected: this.currentPageSize });
      }
    },
    showCreateEvent() {
      this.$modal.show(
        CreateEvent,
        {
          time: this.time
        },
        {
          width: "80%",
          height: "auto",
          scrollable: true,
          clickToClose: false,
          adaptive: true
        }
      );
    }
  }
};
</script>

<style scoped>
.userEvents {
  height: calc(100vh - 50px);
}
.btn:focus {
  box-shadow: none;
}
.active {
  font-weight: 600;
  border-bottom: 6px solid #5b5dcd;
  box-shadow: none;
  color: black;
}
</style>