<template>
  <div class="userEventDetail pt-4 mx-4" v-if="event != null">
    <button v-on:click="showDetailForPost()" class="btn btn-primary">Back</button>
    <form>
      <div class="mt-4">
        <h5>Title</h5>
        <input
          v-model="event.title"
          type="text"
          class="form-control w-100"
          :disabled="!isEditingEvent"
          required="required"
        />
      </div>
      <div class="my-2">
        <h5>Type</h5>
        <select
          class="form-control w-50"
          v-model="event.type"
          :disabled="!isEditingEvent"
          required="required"
        >
          <option value="MOVIE">MOVIE</option>
          <option value="PARTY">PARTY</option>
          <option value="MUSIC">MUSIC</option>
          <option value="SPORT">SPORT</option>
          <option value="CHILL">CHILL</option>
          <option value="OTHER">OTHER</option>
        </select>
      </div>
      <div class="my-2">
        <h5>Author</h5>
        <input
          v-model="event.author"
          type="text"
          class="form-control w-50"
          :disabled="true"
          required="required"
        />
      </div>
      <div class="my-2">
        <h5>Creation date</h5>
        <input
          class="form-control w-50"
          :disabled="true"
          :value="event.createdAt && new Date(event.createdAt).toISOString().split('T')[0]"
          type="date"
        />
      </div>
      <div class="my-2">
        <h5>Event date</h5>
        <div class="row">
          <input
            class="form-control w-50 mx-3"
            type="date"
            :disabled="!isEditingEvent"
            v-model="event.eventDate"
          />
          <input
            class="form-control w-25"
            type="time"
            :disabled="!isEditingEvent"
            v-model="event.eventTimeParsed"
          />
        </div>
      </div>
      <div class="my-2">
        <h5>About</h5>
        <textarea
          v-model="event.content"
          class="form-control form-group"
          :disabled="!isEditingEvent"
          rows="3"
          required="required"
        ></textarea>
      </div>
      <div v-if="user.username == event.author" class="text-center">
        <button
          class="btn btn-primary m-2"
          :disabled="isEditingEvent"
          v-on:click="isEditingEvent = true"
        >EDIT</button>
        <button
          class="btn btn-success m-2"
          :disabled="!isEditingEvent"
          v-on:click="editEvent()"
        >SAVE</button>
        <button class="btn btn-danger m-2" v-on:click="deleteEvent">DELETE</button>
      </div>
      <div class="my-2">
        <h5>Comments</h5>
      </div>
      <div class="my-2 text-right">
        <input
          v-model="postComment"
          type="text"
          class="comment-input w-100 form-group bg-light"
          rows="1"
          :disabled="false"
        />
        <button class="btn btn-primary" @click="postCommentForEvent()">post</button>
      </div>
      <div>
        <table
          id="dtBasicExample"
          class="table table-striped table-bordered table-sm text-left"
          cellspacing="0"
          width="100%"
        >
          <thead class="thead-light">
            <tr>
              <th scope="col">Username</th>
              <th scope="col">Comment</th>
              <th scope="col">Time created</th>
            </tr>
          </thead>
          <tbody v-for="comment in event.comments" :key="comment.id">
            <tr>
              <th class="col-2 text-center">{{ comment.author }}</th>
              <td class="m-2">{{ comment.content }}</td>
              <td class="col-3">{{ formatDate(comment.createdAt) }}</td>
            </tr>
          </tbody>
        </table>
      </div>
    </form>
  </div>
</template>

<script>
import { mapState, mapActions, mapMutations } from "vuex";
import moment from "moment";
export default {
  name: "UserEventDetail",
  data() {
    return {
      isEditingEvent: false,
      postComment: null
    };
  },

  computed: {
    ...mapState(["event", "user"])
  },

  methods: {
    ...mapActions([
      "loadEventDetail",
      "addComment",
      "editEventDetail",
      "removeCurrentEvent"
    ]),
    ...mapMutations(["SET_EVENT", "SET_FIRST_PAGE"]),

    showDetailForPost: function() {
      this.SET_FIRST_PAGE();
      this.SET_EVENT(null);
    },
    postCommentForEvent() {
      if (this.postComment != null) {
        this.addComment(this.postComment);
        this.postComment = "";
      }
    },
    formatDate(date) {
      return moment(date).format("LLL");
    },
    editEvent() {
      const event = {
        id: this.event.id,
        title: this.event.title,
        type: this.event.type,
        author: this.event.author,
        content: this.event.content,
        createdAt: this.event.createdAt,
        eventTime:
          this.event.eventDate.split("T")[0] +
          "T" +
          this.event.eventTimeParsed +
          "Z",
        latitude: this.event.latitude,
        longitude: this.event.longitude
      };
      this.isEditingEvent = false;
      this.editEventDetail(event);
    },
    deleteEvent() {
      this.$confirm("Are you sure?").then(() => {
        this.SET_FIRST_PAGE();
        this.removeCurrentEvent(this.event.id);
      });
    }
  }
};
</script>

<style scoped>
.userEventDetail {
  height: calc(100vh - 50px);
}
.comment-input {
  border: 0px;
  border-bottom: 1px black solid;
}
</style>