<template>
  <div class="container">
    <div>
      <nav class="sidenav">
        <a>
          <router-link to="events">Events</router-link>
        </a>
        <a>
          <router-link to="users">Users</router-link>
        </a>
        <a>
          <button class="btn btn-primary mx-2" v-on:click="showCreateUser()">Create</button>
          <modals-container />
        </a>
      </nav>
    </div>
    <div class="container" v-if="userDetail == null">
      <div class="row my-4">
        <div class="col-4">
          <button class="btn btn-primary" type="submit" @click="loadUserList()">Refresh</button>
        </div>
        <div class="col-4">
          <input
            class="form-control"
            type="text"
            placeholder="Find user"
            v-model="searchValue"
            aria-label="Search"
          />
        </div>
        <button class="btn btn-primary" type="submit" @click="findUser()">Find</button>
      </div>
      <div class="text-center">
        <table
          id="dtBasicExample"
          class="table table-striped table-bordered table-sm text-left"
          cellspacing="0"
          width="100%"
        >
          <thead class="thead-light">
            <tr>
              <th scope="col">Username</th>
              <th scope="col">First Name</th>
              <th scope="col">Last Name</th>
              <th scope="col">Email</th>
              <th scope="col"></th>
            </tr>
          </thead>
          <tbody v-for="(user, index) in users" v-bind:key="index">
            <tr>
              <th class="text-center" scope="row">{{ user.username }}</th>
              <td>{{ user.firstname }}</td>
              <td>{{ user.lastname }}</td>
              <td>{{ user.email }}</td>
              <td class="text-center">
                <button
                  class="btn-sm btn-primary"
                  v-on:click="showDetailForUser(user.username)"
                >Edit</button>
              </td>
            </tr>
          </tbody>
        </table>
        <!-- <button class="btn btn-primary" v-on:click="loadMore">Load more</button> -->
      </div>
    </div>
    <div class="container py-2" v-else>
      <AdminDetailUser :isCreatingNewUser="this.isCreatingNewUser" />
    </div>
  </div>
</template>

<script>
import { mapState, mapActions, mapMutations } from "vuex";

import AdminDetailUser from "@/components/admin/AdminDetailUser.vue";
import CreateUser from "@/components/CreateUser.vue";

export default {
  data() {
    return {
      typeSelected: "ALL",
      numberToShowSelected: "10",
      isCreatingNewUser: false,
      searchValue: null
    };
  },
  components: {
    AdminDetailUser
  },
  computed: {
    ...mapState(["user", "userDetail", "users"])
  },
  created: function() {
    if (this.user.role !== "ADMIN") {
      this.$toasted.show("Unauthorized", {
        theme: "outline",
        position: "top-center",
        duration: 3000
      });
      this.$router.push("/admin");
    } else {
      this.loadUserList();
    }
  },
  methods: {
    ...mapActions(["loadUsers", "loadUserDetail", "searchUser"]),
    ...mapMutations(["SET_FIRST_PAGE", "SET_USER"]),

    loadUserList() {
      this.SET_FIRST_PAGE;
      this.loadUsers();
    },
    showDetailForUser(username) {
      this.isCreatingNewUser = false;
      this.loadUserDetail(username);
    },
    findUser() {
      if (this.searchValue != null) {
        this.searchUser(this.searchValue);
      }
    },
    showCreateUser() {
      console.log("HI");
      this.$modal.show(
        CreateUser,
        {
          time: this.time
        },
        {
          width: "60%",
          height: "auto",
          scrollable: true,
          clickToClose: false,
          adaptive: true
        },
        {
          // "before-close": this.beforeClose
        }
      );
    }
  }
};
</script>

