<template>
  <div id="app">
    <nav class="navbar bg-primary px-5">
      <a class="navbar-brand m-0 p-0" href="#">
        <img src="./assets/logo.png" width="200" alt />
      </a>
      <div v-if="user.token">
        <button class="btn btn-sm" v-on:click="logout">Log out</button>
      </div>
      <div v-else>
        <router-link to="/login">
          <button class="btn btn-sm">Log in</button>
        </router-link>
      </div>
    </nav>
    <router-view />
  </div>
</template>

<script>
import { mapState, mapActions } from "vuex";
export default {
  computed: mapState(["user"]),
  methods: {
    ...mapActions(["userLogout"]),

    logout: function() {
      this.userLogout();
    }
  }
};
</script>

