<template>
  <div id="createEvent">
    <form class="row">
      <div class="col-5 m-3">
        <button class="btn btn-primary my-2" @click="close">Close</button>
        <div class="my-2">
          <h5>Title</h5>
          <input
            v-model="title"
            type="text"
            class="form-control"
            :disabled="!isCreatingNewEvent"
            required="required"
          />
        </div>
        <div class="my-2">
          <h5>Type</h5>
          <select class="form-control w-50" v-model="type" required="required">
            <option value="MOVIE">MOVIE</option>
            <option value="PARTY">PARTY</option>
            <option value="MUSIC">MUSIC</option>
            <option value="SPORT">SPORT</option>
            <option value="CHILL">CHILL</option>
            <option value="OTHER">OTHER</option>
          </select>
        </div>
        <div class="my-2">
          <h5>Author</h5>
          <input
            v-model="user.username"
            type="text"
            class="form-control w-50"
            :disabled="user.role!=='ADMIN'"
            required="required"
          />
        </div>
        <div class="my-2">
          <h5>Creation date</h5>
          <input
            class="form-control w-50"
            :disabled="true"
            :value="createdAt && new Date(createdAt).toISOString().split('T')[0]"
            type="date"
          />
        </div>
        <div class="my-2">
          <h5>Event date</h5>
          <div class="row">
            <input class="form-control w-50 mx-3" type="date" v-model="date" />
            <input class="form-control w-25" type="time" v-model="time" />
          </div>
        </div>
        <div class="my-2">
          <h5>About</h5>
          <textarea v-model="content" class="form-control form-group" rows="3" required="required"></textarea>
        </div>
        <div class="text-center">
          <button class="btn btn-success m-2" @click="saveEvent">SAVE</button>
        </div>
      </div>
      <GmapMap
        :center="{lat: 50.077555, lng: 14.436008}"
        :zoom="15"
        style="width: 100%; height: 700px"
        class="col-6 m-2"
      >
        <GmapMarker
          :position="{lat: position.lat,lng: position.lng}"
          :clickable="true"
          :draggable="true"
          @drag="updateCoordinates"
          @dragend="updateEventFinalPosition"
        />
      </GmapMap>
    </form>
  </div>
</template>

<script>
import { gmapApi } from "vue2-google-maps";
import { mapActions, mapState } from "vuex";
export default {
  name: "CreateEvent",
  data() {
    return {
      isCreatingNewEvent: true,
      title: "",
      type: "",
      author: "",
      content: "",
      createdAt: new Date(),
      date: "",
      time: "00:00",
      latitude: 50.077555,
      longitude: 14.436008,

      position: {
        lat: 50.077555,
        lng: 14.436008
      }
    };
  },
  computed: {
    ...mapState(["user"]),
    google: gmapApi
  },
  methods: {
    ...mapActions(["addEvent"]),
    updateCoordinates(location) {
      this.position.lat = location.latLng.lat();
      this.position.lng = location.latLng.lng();
    },
    updateEventFinalPosition(location) {
      this.latitude = location.latLng.lat();
      this.longitude = location.latLng.lng();
    },
    saveEvent() {
      const event = {
        title: this.title,
        type: this.type,
        author: this.user.username,
        content: this.content,
        latitude: this.latitude,
        longitude: this.longitude,
        eventTime: new Date(this.date + "T" + this.time + "Z")
      };

      console.log(event);
      this.addEvent(event);
      this.$emit("close");
    },
    close() {
      this.$emit("close");
    }
  }
};
</script>

<style scoped>
#createEvent {
  color: black;
  border: 3px solid #007bff;
}
</style>