<template>
  <div id="signup" class="bg-primary">
    <h2 class="text-center">Sign up</h2>
    <form>
      <div class="signup-form">
        <div class="form-group">
          <input
            type="text"
            class="form-control"
            v-model="input.username"
            placeholder="Username"
            required="required"
          />
        </div>
        <div class="form-group">
          <input
            type="email"
            class="form-control"
            v-model="input.email"
            placeholder="Email Address"
            required="required"
          />
        </div>
        <div class="form-group">
          <input
            type="password"
            class="form-control"
            v-model="input.password"
            placeholder="Password"
            required="required"
          />
        </div>
        <div class="form-group">
          <input
            type="password"
            class="form-control"
            v-model="input.passwordConfirm"
            placeholder="Confirm Password"
            required="required"
          />
        </div>
        <div class="form-group">
          <button v-on:click="registerUser" class="btn btn-primary w-100">Sign Up</button>
        </div>
        <div class="text-center login-text">
          Already have an account?
          <a v-on:click="login()">Login here</a>
        </div>
      </div>
    </form>
  </div>
</template>

<script>
import { mapActions, mapMutations } from "vuex";

export default {
  name: "signup",

  data() {
    return {
      input: {
        username: "",
        email: "",
        password: "",
        passwordConfirm: ""
      },
      hasAccount: true
    };
  },

  methods: {
    ...mapActions(["saveUser"]),
    ...mapMutations(["SET_IS_REGISTERED"]),

    registerUser() {
      if (this.input.username != "" && this.input.password != "") {
        if (this.input.password != this.input.passwordConfirm) {
          this.$toasted.show("Password doesn't match!", {
            theme: "outline",
            position: "top-center",
            duration: 2000
          });
          (this.input.password = ""), (this.input.passwordConfirm = "");
        } else {
          this.saveUser(this.input);
        }
      }
    },
    login() {
      this.SET_IS_REGISTERED(true);
    }
  }
};
</script>

<style scoped>
#signup {
  padding: 30px;
  border-radius: 6%;
  font-size: 1.2em;
}
.text-center a {
  color: white;
  text-decoration: underline;
}
.text-center a:hover {
  color: black;
  text-decoration: underline;
}
button {
  background-color: #ffffff;
  border: 0;
  color: black;
  font-weight: 600;
}
</style>
