<template>
  <div class="container">
    <div class="row my-3">
      <div class="col-6">
        <input
          class="form-control"
          type="text"
          placeholder="Find user"
          v-model="searchValue"
          aria-label="Search"
        />
      </div>
      <button class="btn btn-primary" type="submit" @click="findUser()">Find</button>
    </div>
    <div class="text-center" v-if="users != null">
      <table
        class="table table-striped table-bordered table-sm text-left"
        cellspacing="0"
        width="100%"
      >
        <thead class="thead-light">
          <tr>
            <th scope="col">Username</th>
            <th scope="col">First Name</th>
            <th scope="col">Last Name</th>
            <th scope="col">Email</th>
          </tr>
        </thead>
        <tbody v-for="(user, index) in users" v-bind:key="index">
          <tr @click="chooseUser(user.username)">
            <th class="text-center" scope="row">{{ user.username }}</th>
            <td>{{ user.firstname }}</td>
            <td>{{ user.lastname }}</td>
            <td>{{ user.email }}</td>
          </tr>
        </tbody>
      </table>
    </div>
  </div>
</template>

<script>
import { mapState, mapActions } from "vuex";
export default {
  name: "FindUser",
  data() {
    return {
      searchValue: ""
    };
  },
  computed: {
    ...mapState(["users", "event"])
  },
  methods: {
    ...mapActions(["searchUser"]),

    findUser() {
      if (this.searchValue != null) {
        this.searchUser(this.searchValue);
      }
    },
    chooseUser(username) {
      this.event.author = username;
      this.$emit("close");
    }
  }
};
</script>

<style scoped>
.table-striped tbody tr:nth-of-type(2n + 1):hover {
  background-color: #007bff;
  color: white;
  border-color: #007bff;
  cursor: pointer;
}
</style>