<template>
  <div class="container">
    <form>
      <div class="login-form">
        <div class="form-group">
          <input
            type="text"
            class="form-control"
            v-model="username"
            placeholder="Username"
            required="required"
          />
        </div>
        <div class="form-group">
          <input
            type="password"
            class="form-control"
            v-model="password"
            placeholder="Password"
            required="required"
          />
        </div>
        <div class="form-group">
          <button v-on:click="login" class="btn btn-primary w-100">Login</button>
        </div>
      </div>
    </form>
  </div>
</template>

<script>
import { mapActions, mapState } from "vuex";

export default {
  name: "AdminSignin",
  data() {
    return {
      username: "",
      password: ""
    };
  },
  created: function() {
    this.logingWithStoredToken();
    if (this.user.role === "ADMIN") {
      this.$router.push("/admin/events");
    }
  },
  computed: {
    ...mapState(["user"])
  },
  methods: {
    ...mapActions(["adminSignin", "logingWithStoredToken"]),

    login() {
      if (this.username != "" && this.password != "") {
        const userAuth = {
          username: this.username,
          password: this.password
        };
        this.adminSignin(userAuth);
      }
    },
    loginWithToken() {
      this.logingWithStoredToken();
    }
  }
};
</script>

<style scoped>
.container {
  height: calc(100vh - 50px);
  display: flex;
  justify-content: center;
  align-items: center;
}
</style>