<template>
  <div id="signin">
    <h2 class="text-center">Login</h2>
    <form>
      <div class="login-form">
        <div class="form-group">
          <input
            type="text"
            class="form-control"
            v-model="username"
            placeholder="Username"
            required="required"
          />
        </div>
        <div class="form-group">
          <input
            type="password"
            class="form-control"
            v-model="password"
            placeholder="Password"
            required="required"
          />
        </div>
        <div class="form-group">
          <button v-on:click="login" class="btn btn-primary w-100">Login</button>
        </div>
        <p class="text-center">
          <a href="#" v-on:click="register">Create an Account</a>
        </p>
      </div>
    </form>
  </div>
</template>

<script>
import { mapActions, mapMutations } from "vuex";

export default {
  name: "signin",

  data() {
    return {
      username: "",
      pw: ""
    };
  },

  components: {},

  methods: {
    ...mapActions(["userSignin"]),
    ...mapMutations(["SET_IS_REGISTERED"]),

    login() {
      if (this.username != "" && this.password != "") {
        const userAuth = {
          username: this.username,
          password: this.password
        };
        this.userSignin(userAuth);
      }
    },
    register() {
      this.SET_IS_REGISTERED(false);
    }
  }
};
</script>

<style scoped>
#signin {
  padding: 40px;
  padding-bottom: 20px;
  /* border-radius: 6%; */
  border: none;
  font-size: 1.2em;
}

h2 {
  color: black;
}
.text-center a {
  color: black;
  text-decoration: underline;
}
.text-center a:hover {
  color: #007bff;
  text-decoration: underline;
}
</style>
