<template>
  <div>
    <div>
      <nav class="sidenav">
        <a>
          <router-link to="events">Events</router-link>
        </a>
        <a>
          <router-link to="users">Users</router-link>
        </a>
        <a>
          <button class="btn btn-primary mx-2" v-on:click="showCreateEvent()">Create</button>
          <modals-container />
        </a>
      </nav>
    </div>
    <div class="container" v-if="event == null">
      <div class="row mt-4 mb-2">
        <div class="col-4">
          <button class="btn btn-primary" type="submit" @click="loadEventsForPage()">Refresh</button>
        </div>
        <div class="col-4">
          <input class="form-control" type="text" placeholder="Search" aria-label="Search" />
        </div>
        <button class="btn btn-primary" type="submit" @click="searchEvents">Search</button>
      </div>
      <div class="row mb-4">
        <div class="col-4 mx-2"></div>
        <div class="btn-group col-2">
          <h4 class="m-1">Type:</h4>
          <select class="form-control" v-model="typeSelected" @change="loadEventsForTypeSelected">
            <option value="MOVIE">MOVIE</option>
            <option value="PARTY">PARTY</option>
            <option value="MUSIC">MUSIC</option>
            <option value="OTHER">OTHER</option>
            <option value="ALL" selected="selected">ALL</option>
          </select>
        </div>
        <div class="btn-group">
          <h4 class="m-1">Entries:</h4>
          <select class="form-control" v-model="numberToShowSelected" @change="loadEventsForPage">
            <option value="5">5</option>
            <option value="10" selected="selected">10</option>
            <option value="15">15</option>
            <option value="20">20</option>
          </select>
        </div>
      </div>
      <div class="text-center">
        <table
          id="dtBasicExample"
          class="table table-striped table-bordered table-sm text-left"
          cellspacing="0"
          width="100%"
        >
          <thead class="thead-light">
            <tr>
              <th scope="col">ID</th>
              <th scope="col">Type</th>
              <th scope="col">Title</th>
              <th scope="col">Author</th>
              <th scope="col"></th>
            </tr>
          </thead>
          <tbody v-for="(event, index) in events" v-bind:key="index">
            <tr>
              <th class="text-center" scope="row">{{ event.id }}</th>
              <th class="text-center" scope="row">{{ event.type }}</th>
              <td>{{ event.title }}</td>
              <td>{{ event.author }}</td>
              <td class="text-center">
                <button class="btn-sm btn-primary" v-on:click="showDetailForEvent(event.id)">detail</button>
              </td>
            </tr>
          </tbody>
        </table>
        <button class="btn btn-primary m-2" v-on:click="loadMore">Load more</button>
      </div>
    </div>
    <div class="container-fluid w-75 py-2" v-else>
      <AdminDetailEvent :isCreatingNewEvent="this.isCreatingNewEvent" />
    </div>
  </div>
</template>

<script>
import { mapActions, mapState, mapMutations } from "vuex";
import AdminDetailEvent from "@/components/admin/AdminDetailEvent";
import CreateEvent from "@/components/CreateEvent";

export default {
  name: "Dashboard",
  data() {
    return {
      typeSelected: "ALL",
      numberToShowSelected: "10",
      isCreatingNewEvent: Boolean
    };
  },
  components: {
    AdminDetailEvent
  },
  computed: {
    ...mapState(["user", "events", "event", "isShown", "currentPageSize"])
  },
  created: function() {
    if (this.user.role !== "ADMIN") {
      this.$router.push("/admin");
    } else {
      this.SET_FIRST_PAGE();
      this.loadEvents(this.currentPageSize);
    }
  },
  methods: {
    ...mapActions([
      "loadEvents",
      "loadMoreEvents",
      "loadEventDetail",
      "loadEventsByType"
    ]),
    ...mapMutations([
      "SET_FIRST_PAGE",
      "SET_NEXT_PAGE",
      "SET_CURRENT_PAGE_SIZE",
      "SET_EVENT"
    ]),

    loadMore() {
      this.SET_NEXT_PAGE();
      this.loadMoreEvents(this.numberToShowSelected);
    },
    createNewEvent() {
      this.SET_EVENT({});

      this.isCreatingNewEvent = true;
    },
    loadEventsForPage() {
      this.SET_FIRST_PAGE();
      this.SET_CURRENT_PAGE_SIZE(this.numberToShowSelected);
      this.loadEvents();
    },
    loadEventsForTypeSelected() {
      this.SET_FIRST_PAGE();

      if (this.typeSelected == "ALL") {
        this.loadEvents(this.numberToShowSelected);
      } else {
        const payload = {
          type: this.typeSelected,
          selected: this.numberToShowSelected
        };
        this.loadEventsByType(payload);
      }
    },
    showDetailForEvent(id) {
      this.isCreatingNewEvent = false;
      this.loadEventDetail(id);
      this.$router.push("event/" + id);
    },
    searchEvents() {
      console.log("hi");
    },
    showCreateEvent() {
      this.$modal.show(
        CreateEvent,
        {
          time: this.time
        },
        {
          width: "80%",
          height: "auto",
          scrollable: true,
          clickToClose: false,
          adaptive: true
        },
        {
          // "before-close": this.beforeClose
        }
      );
    }
  }
};
</script>

