import Vue from 'vue'
import VueRouter from 'vue-router'
import Main from '../views/Main.vue'
import Admin from '../views/Admin.vue'
import Login from '../views/Login.vue'
import AdminEvents from '../views/admin/AdminEvents.vue'
import AdminDetailEvent from '@/components/admin/AdminDetailEvent.vue'
import AdminUsers from '../views/admin/AdminUsers.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'home',
    component: Main,
    meta:
      { title: "Eventsnow" },
  },
  {
    path: '/admin',
    name: 'admin',
    component: Admin,
    meta:
      { title: "Eventsnow-admin" },
  },
  {
    path: '/admin/events',
    name: 'admin-events',
    component: AdminEvents,
    meta:
      { title: "Eventsnow-admin-events" },
  },
  {
    path: '/admin/event/:eventId',
    name: 'admin-event-detail',
    component: AdminDetailEvent,
    meta:
      { title: "Eventsnow-admin-event" },
  },
  {
    path: '/admin/users',
    name: 'admin-users',
    component: AdminUsers
  },
  {
    path: '/login',
    name: 'login',
    component: Login
  }
]

const router = new VueRouter({
  routes
})

export default router
