<template>
  <div id="eventSection" class="bg-light">
    <div v-if="event == null">
      <UserEvents />
    </div>
    <div v-else>
      <UserEventDetail />
    </div>
  </div>
</template>

<script>
import UserEvents from "@/components/UserEvents.vue";
import UserEventDetail from "@/components/UserEventDetail.vue";

import { mapState, mapActions } from "vuex";

export default {
  name: "UserEventSection",

  data() {
    return {
      isActive: false
    };
  },

  mounted: function() {
    this.$nextTick(function() {
      this.loadEventsForUsers();
    });
  },

  components: {
    UserEvents,
    UserEventDetail
  },

  computed: {
    ...mapState(["event"])
  },

  methods: {
    ...mapActions(["removePost", "addPost", "loadEventsForUsers", "loadPage"]),

    loadEventList: function() {
      this.loadEvents();
    }
  }
};
</script>

<style scoped>
.eventSection {
  height: calc(100vh - 50px);
  width: auto;
}
</style>
